.footer {
    width: 100%;
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 10px;
  }
  
  .socialMedia svg {
    color: var(--lightest-slate);
    margin: 5px;
    font-size: 70px;
    cursor: pointer;
    transform: scale(0.8);
  }
  
  .footer p {
    color: var(--lightest-slate);
  }
  
  @media only screen and (max-width: 600px) {
    .footer svg {
      font-size: 50px;
    }
  }